@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500;800&display=swap');

:root{
  --cyan : hsl(180, 66%, 49%);
  --dark-voilet : hsl(257, 27%, 26%);
  
  /* Secondary color */
  --red: hsl(0, 87%, 67%);

  /* Natural Color */
--Gray: hsl(0, 0%, 75%);
--Grayish-Violet: hsl(257, 7%, 63%);
--Very-Dark-Blue: hsl(255, 11%, 22%);
--Very-Dark-Violet: hsl(260, 8%, 14%);
}

body{
  font-size: 18px;
  font-family: 'Poppins', sans-serif;
}

.result button {
  background: var(--cyan);
  color: #fff;
  cursor: pointer;
  border: 2px solid red;
  padding: .5rem;
  border-radius: 20px;
  width: 10rem;
}

.result button.copied{
  color: #fff;
  padding: 0.5rem;
  border-radius: 20px;
}

noData{
  color: red;
  font-size: 2rem;
}

.BannerImg{
  background: url(./images//bg-boost-desktop.svg);
  background-position: center;
  background-size: cover;
}